body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    background: linear-gradient(88deg, #8A2387 , #E94057, #F27121);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cardDesc {
  margin: auto;
  margin-top: 5%;
  margin-bottom: 5%;
  
  padding-top: 4%;
  padding-bottom: 4%;
  padding-right: 4%;
  padding-left: 4%;
}

.cardDesc h2{
  padding-top: 10%;
}

.cardH2Font{
  font-size: 2rem;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif
}

.cardMallLogo{
  padding-top: 8%;
  padding-bottom: 8%;
  height: 60%;
  width: 60%;

}



.cardList{
    padding: 5%;
}

.floorDesc{
    padding-left: 5%;
}
searchBox{
  width: 100%;
  height: 20%;
}

.searchBoxInput{
  width: 90%;
  background-color: white;
  padding: 1em;
  text-align: center;
}

