h1 {
  font-family: 'SEGA LOGO FONT';
  font-weight: 200;
  color: rgb(255, 255, 255);
}


body {
  margin: 40px;
}



.wrapper {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  background-color: #fff;
  color: #444;
}

.box {
  background-color: #444;
  color: #fff;
  border-radius: 5px;
  padding: 20px;
  font-size: 150%;
}

.img {
  height: auto;
  max-height: 1120px;
  width: auto;
  max-width: 695px;
}

